import React, { useContext, useEffect } from "react";
import { IntlProvider } from "react-intl";
import { RootContextProvider, RootContext } from "./src/contexts/rootContext";
import locale from "./locale";

export const wrapRootElement = ({ element }) => (
  <RootContextProvider>
    <IntlProvider locale="en" messages={locale["en"]}>
      <Container>{element}</Container>
    </IntlProvider>
  </RootContextProvider>
);

const Container = React.memo(({ children }) => {
  const { fetchProfile } = useContext(RootContext);
  useEffect(() => {
    fetchProfile();
  }, [fetchProfile]);
  return children;
});
